export default {
	data() {
		return {
			details:{
				name:'',
				tel:'',
				city:['北京市','北京市','北京市'],
				details:'',
				default:0,
			},
		};
	},
	onLoad: function() {
		let details = uni.getStorageSync('address-edit');
		if(details){
			details.city = details.city.split(',')
			details.default = details.default == 1 ? true :false
			this.details = details
			uni.removeStorageSync('address-edit');
		}
	},
	methods: {
		OnSubmit() {
			let details = JSON.parse(JSON.stringify(this.details))
			details.city = details.city.join(',')
			details.default = details.default ? 1 : 0
			let errMsg = '';
			if(!details.name){
				errMsg = "请输入姓名"
			}else if(!details.tel){
				errMsg = "请输入手机号"
			}else if(!details.tel){
				errMsg = "请选择城市"
			}else if(!details.details){
				errMsg = "请输入详情地址"
			}
			if(errMsg){
				uni.showToast({
					icon: 'none',
					title: errMsg,
					duration: 2000
				});
				return false;
			}
			if(details.id > 0){
				this.$minApi
					.addressEdit(details)
					.then(res => {
						uni.setStorageSync('address-load', 1);
						uni.navigateBack({
						    delta: 1
						});
					})
					.catch(err => {
						console.log(err);
					});
			}else {
				this.$minApi
					.addressAdd(details)
					.then(res => {
						uni.setStorageSync('address-load', 1);
						uni.navigateBack({
						    delta: 1
						});
					})
					.catch(err => {
						console.log(err);
					});
			}
		},
		
		RegionChange(e) {
			this.details.city = e.detail.value
		},
		SwitchChange(e) {
			this.details.default = e.detail.value
		},
		inputName(e) {
			this.details.name = e.detail.value
		},
		inputTel(e) {
			this.details.tel = e.detail.value
		},
		inputDetails(e) {
			this.details.details = e.detail.value
		},
	}
}
